body h1, body h2, body h3, body h4, body h5, body h6 {
  font-family: lora;
}

footer {
  background: #253b81;
  min-height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  color: #fff;
  font-family: lora;
}

.center {
  text-align: center;
}

.emphasis {
  font-weight: 600;
  text-decoration: underline;
}

.container {
  background: #eceeef;
  flex: 1;
  padding-top: 0.75em;
}

.container .container-standard {
  padding: 5px 5%;
}

.container .container-flex {
  display: flex;
  justify-content: space-around;
}

.container .container-flex .main-info {
  flex-basis: 60%;
}

.container .container-flex .news {
  flex-basis: 30%;
}

.container .container-flex .news h3 {
  text-align: center;
}

.site {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.site-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.container-header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  z-index: 10;
  width: 100%;
  padding: 0 5%;
  background: rgba(37, 59, 129, 0.75);
  color: #fff;
}

.container-header h1 {
  margin: 0;
}

.header-img {
  position: relative;
  display: block;
}

.header-img .text-box {
  position: relative;
  display: flex;
  height: 400px;
  flex-direction: column;
  z-index: 1;
  width: 100%;
  justify-content: center;
}

.header-img .text-box .top-text {
  margin-top: 1rem;
  margin-bottom: 0;
  color: #fff;
  font-size: 3.5em;
  font-family: quicksand;
  text-align: center;
}

.header-img .text-box .bottom-text {
  color: #fff;
  font-size: 4em;
  font-family: blackjack;
  text-align: center;
}

.header-img .home {
  position: absolute;
  top: 0;
  background: url("/static/images/basiclia1.jpg");
  background-position: 50%;
  background-size: cover;
  display: block;
  width: 100%;
  height: 400px;
}

.header-img .home::after {
  content: '';
  background: rgba(0, 0, 0, 0.75);
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

